@import '../../styles/mixins';

.postsPage {
  width: 100vw;

  &__totalItems {
    margin-bottom: 32px;

    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    @include onTablet {
      margin-bottom: 40px;
    }
  }
}

.catalog {
  margin-bottom: 24px;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 45px;
  row-gap: 45px;

  @include onTablet {
    grid-template-columns: repeat((1, 1fr));
    justify-items: center;
  }

  @include onLaptop {
    grid-template-columns: repeat(2, 1fr);
  }

  @include onDesktop {
    grid-template-columns: repeat(3, 1fr);
  }
}
